.file-menu{
  align-self: stretch;
}
.file-menu-button{
  background-color: #1979ca;
  color: white;
  padding: 0.5em 1em;
  border: none;
}
.file-menu-button:hover{
  background-color: #2f95e9;
}
.file-menu-button:active{
  background-color: #1468ac;
}
.file-menu{
  position: relative;
  z-index: 10;
  display: inline-block;
  background: rgb(245,245,245)
}
 .file-menu-items{
  display: none;
  width: 15em;
  position: absolute;
  top: 100%;
  box-shadow: 3px 3px 6px darkgrey;
  background: white;
  z-index: 10;
}
 .file-menu_open .file-menu-items{
  display: block;
}
 .file-menu-item{
  display: block;
  width: 100%;
  height: 4em;
  line-height: 4em;
  text-align: left;
  padding-left: 3em;
  background: white;
  border: 1px solid transparent;
  font-size: 0.8em;
  color: black;
  text-decoration: none;
  cursor: default;
}
.file-menu-item input{
  display: none;
}
.file-menu-item:hover{
  background: var(--hover-background-color);
  border: 1px solid var(--hover-border-color);
}